import React, { Fragment } from 'react'

function RadioWithText({radioBtn=[{label: 'Google',value: 'Google'},{label: 'Facebook',value: 'Facebook'}],handleChange,stepKey,name}) {
    const handleChangeBtn =(event)=>{
        handleChange(stepKey,event,name)
    }
    return (
        <Fragment>
            
  
        {radioBtn.map(({value,label,checked})=>{
            return(
                <div className="column is-6-mobile bg-color" key={label}>
        <div className="control">
        <label className="radio ">
            <input type="radio" name={label} className="mr-2"   value={value}  
                                   checked={checked} 
                                   onChange={handleChangeBtn}  />
    {label}
  </label>
                </div>
                </div>
            )
        })
        }
        </Fragment>
    )
}

export default RadioWithText
