import React from 'react';
import PropTypes from 'prop-types';
import micIcon from '../imgs/mic.png';

const Input = ({ type = 'text', placeholder, name, value, onChange, error ,showLabel=true,label }) => {
  return(
    <div className="field mb-2">
  <label className="label flow_label">{label}</label>
  <div className={`control`} style={{display:'inline'}}>
  <input
        className={error ? "input flow_input is-danger" : "input flow_input"} 
        type={type} 
        placeholder={placeholder}
        name={name}
        value={value}
        onChange={onChange}
        autoComplete="off"
      />
      <span>
    <img src={micIcon} alt={"mic"} style={{width:'35px',paddingTop:'3px'}} />
  </span>   
  </div>
  {error && <div className="has-text-danger-dark">{error}</div>}

</div>
    // <div className="mb-5">

     
    // </div>
  );
}

Input.propTypes = {
  type: PropTypes.string,
  placeholder: PropTypes.string,
  name: PropTypes.string.isRequired,
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.object,
    PropTypes.number
  ]),
  onChange: PropTypes.func.isRequired
}

export default Input;